.App {
  text-align: center;
}
.app {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  color: #f0f4f8;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #0f1729 0%, #1a2847 25%, #162d4a 50%, #1e3a5f 75%, #0d1b2a 100%);
}

.animated-bg {
  position: absolute;
  inset: 0;
  z-index: 0;
  overflow: hidden;
}

.blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.6;
  mix-blend-mode: screen;
}

.b1 { width: 520px; height: 520px; background: #ff6b9d; left: -10%; top: -10%; animation: floatA 12s ease-in-out infinite; }
.b2 { width: 420px; height: 420px; background: #4facfe; right: -5%; top: 10%; animation: floatB 14s ease-in-out infinite; }
.b3 { width: 360px; height: 360px; background: #00d4ff; left: 20%; bottom: -15%; animation: floatC 16s ease-in-out infinite; }

@keyframes floatA { 0% { transform: translateY(0) translateX(0) scale(1);} 50% { transform: translateY(40px) translateX(30px) scale(1.05);} 100% { transform: translateY(0) translateX(0) scale(1);} }
@keyframes floatB { 0% { transform: translateY(0) translateX(0) scale(1);} 50% { transform: translateY(-30px) translateX(-20px) scale(1.03);} 100% { transform: translateY(0) translateX(0) scale(1);} }
@keyframes floatC { 0% { transform: translateY(0) translateX(0) scale(1);} 50% { transform: translateY(50px) translateX(-10px) scale(0.98);} 100% { transform: translateY(0) translateX(0) scale(1);} }

.nav {
  position: relative;
  z-index: 2;
  display:flex;
  justify-content:space-between;
  align-items:center;
  padding:20px 48px;
  background: rgba(15, 23, 41, 0.7);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255,255,255,0.08);
}

.brand { font-weight:800; font-size:20px; color: #ffffff; letter-spacing: -0.5px; }
.brand .verse { color:#00d4ff; margin-left:8px; font-weight:700; }

.nav-links a { color: rgba(240,244,248,0.85); margin-left:24px; text-decoration:none; font-weight:600; font-size:15px; transition: color .2s ease; }
.nav-links a:hover { color:#00d4ff; }

main { position: relative; z-index: 2; padding: 48px 32px 100px; }

.hero { text-align: center; padding:140px 20px 80px; }
.hero h1 { font-size: clamp(32px, 6.5vw, 64px); margin:0 0 18px; line-height:1.1; color: #ffffff; letter-spacing: -0.02em; font-weight:900; text-wrap: balance; animation: fadeInUp .9s both .15s; text-shadow: 0 2px 10px rgba(0,0,0,0.3); }
.hero .lead { max-width:880px; margin:0 auto 24px; color: rgba(240,244,248,0.92); font-weight:600; opacity:.98; font-size:18px; line-height:1.6; animation: fadeInUp .9s both .3s; }
.highlight { display:inline-block; background: linear-gradient(90deg,#00d4ff,#0099ff); color:#0f1729; padding:8px 14px; border-radius:10px; margin-left:10px; font-weight:800; box-shadow: 0 8px 20px rgba(0,212,255,0.3); }

.hero-sub { display:flex; gap:12px; justify-content:center; margin-bottom:16px; flex-wrap:wrap; }
.pill { background: rgba(0,212,255,0.1); padding:10px 16px; border-radius:999px; font-weight:700; color:#00d4ff; box-shadow: 0 8px 20px rgba(0,212,255,0.15); border: 1px solid rgba(0,212,255,0.3); }

.ctas { display:flex; gap:16px; justify-content:center; margin-top:24px; animation: fadeInUp .9s both .45s; flex-wrap:wrap; }
.btn { padding:14px 24px; border-radius:12px; text-decoration:none; font-weight:700; font-size:15px; transition: all .3s ease; cursor:pointer; border:none; }
.btn.primary { background: linear-gradient(135deg,#00d4ff,#4facfe); color:#0f1729; box-shadow: 0 8px 24px rgba(0,212,255,0.35); }
.btn.primary:hover { transform: translateY(-3px); box-shadow: 0 12px 40px rgba(0,212,255,0.5); }
.btn.ghost { border:2px solid rgba(0,212,255,0.4); color: #00d4ff; background:transparent; }
.btn.ghost:hover { background: rgba(0,212,255,0.1); border-color: rgba(0,212,255,0.8); }

.services { display:flex; gap:20px; justify-content:center; margin-top:40px; flex-wrap:wrap; }
.card { background: linear-gradient(135deg, rgba(0,212,255,0.05), rgba(79,172,254,0.05)); padding:22px; border-radius:16px; width:280px; text-align:left; box-shadow: 0 8px 32px rgba(0,212,255,0.1); border: 1px solid rgba(0,212,255,0.15); transition: all .3s ease; }
.card:hover { transform: translateY(-6px); box-shadow: 0 16px 48px rgba(0,212,255,0.2); border-color: rgba(0,212,255,0.3); }
.card h3 { margin-top:0; color:#fff; font-size:18px; font-weight:700; }
.card p { color: rgba(240,244,248,0.85); line-height:1.6; }

.work { margin-top:60px; text-align:center; }
.grid { display:flex; gap:20px; justify-content:center; margin-top:20px; flex-wrap:wrap; }
.tile { width:220px; height:120px; border-radius:14px; background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.08)); display:flex; align-items:center; justify-content:center; font-weight:700; border: 1px solid rgba(0,212,255,0.2); transition: all .3s ease; }
.tile:hover { transform: translateY(-4px); background: linear-gradient(135deg, rgba(0,212,255,0.12), rgba(79,172,254,0.12)); box-shadow: 0 12px 30px rgba(0,212,255,0.15); }

footer { position: relative; z-index:2; padding:40px 32px; margin-top:80px; border-top:1px solid rgba(255,255,255,0.08); background: rgba(15, 23, 41, 0.5); }
.footer-inner { display:flex; justify-content:space-between; gap:20px; align-items:center; max-width:1200px; margin:0 auto; }
.footer-inner strong { color:#00d4ff; }
.footer-inner p { color: rgba(240,244,248,0.85); margin:0; }
.foot-links { display:flex; gap:16px; flex-wrap:wrap; }
.foot-links a { color: rgba(240,244,248,0.8); text-decoration:none; font-weight:600; font-size:14px; transition: color .2s ease; }
.foot-links a:hover { color:#00d4ff; }
.copyright { margin-top:16px; color: rgba(240,244,248,0.6); font-size:13px; text-align:center; }

@media (max-width:720px){
  .footer-inner{flex-direction:column; text-align:center}
  .nav-links{display:none}
  .services{flex-direction:column; align-items:center}
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(12px); }
  to { opacity: 1; transform: translateY(0); }
}

/* About page styles */
.page { max-width:1000px; margin:0 auto; padding:40px 20px; color: rgba(240,244,248,0.95); }
.page h2 { color:#ffffff; margin-bottom:12px; font-size:32px; font-weight:800; }
.page .intro { font-size:1.08rem; margin-bottom:12px; background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.06)); padding:18px; border-radius:14px; box-shadow: 0 8px 24px rgba(0,212,255,0.1); border: 1px solid rgba(0,212,255,0.15); line-height:1.7; }
.page .mission { font-weight:800; color:#00d4ff; margin-bottom:16px; font-size:16px; }

/* Supporting pages images and layout */
.support-hero { display:flex; gap:18px; align-items:center; margin-bottom:18px; }
.support-ill, .pricing-ill { width:220px; height:auto; border-radius:12px; box-shadow: 0 12px 30px rgba(0,212,255,0.15); border: 1px solid rgba(0,212,255,0.2); }
.faq-list .faq-item { background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); padding:16px; border-radius:12px; margin-bottom:12px; border: 1px solid rgba(0,212,255,0.15); }
.pricing-grid { display:flex; gap:16px; flex-wrap:wrap; margin-top:20px; }
.plan { background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); padding:20px; border-radius:14px; min-width:240px; box-shadow: 0 8px 24px rgba(0,212,255,0.1); display:flex; flex-direction:column; gap:12px; align-items:flex-start; border: 1px solid rgba(0,212,255,0.15); }
.plan .price { font-size:22px; font-weight:900; color:#00d4ff; }
.plan ul { margin:0; padding-left:16px; color: rgba(240,244,248,0.85); }

@media (max-width:820px){
  .support-hero{flex-direction:column; text-align:center}
  .support-ill, .pricing-ill{width:100%; max-width:480px}
  .pricing-grid{flex-direction:column}
}

/* Testimonials */
.featured-testimonials { margin:40px auto 32px; padding:32px; background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.06)); border-radius:18px; text-align:center; border: 1px solid rgba(0,212,255,0.15); }
.featured-testimonials h2 { margin:0 0 18px; }
.testimonials-row { display:flex; gap:16px; margin-bottom:16px; flex-wrap:wrap; justify-content:center; }
.testi-card { background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); padding:18px; border-radius:12px; max-width:240px; box-shadow: 0 8px 24px rgba(0,212,255,0.1); border: 1px solid rgba(0,212,255,0.15); }
.testi-card .stars { display:flex; gap:4px; margin-bottom:10px; }
.star { color: rgba(240,244,248,0.3); font-size:16px; }
.star.filled { color:#00d4ff; }
.testi-card p { margin:0 0 10px; color: rgba(240,244,248,0.9); font-weight:600; font-size:14px; }
.author { font-weight:700; color:#fff; font-size:12px; }
.testi-link { margin:0; }
.testi-link a { color:#00d4ff; text-decoration:none; font-weight:700; }

.testimonials-page .testimonials-grid { display:grid; grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); gap:18px; margin-top:20px; }
.testimonial-card { background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); padding:20px; border-radius:14px; box-shadow: 0 8px 24px rgba(0,212,255,0.1); transition: transform .22s ease, box-shadow .22s ease; border: 1px solid rgba(0,212,255,0.15); }
.testimonial-card:hover { transform: translateY(-6px); box-shadow: 0 16px 40px rgba(0,212,255,0.2); }
.testimonial-card .stars { display:flex; gap:4px; margin-bottom:12px; }
.testimonial-card .feedback { margin:0 0 12px; color: rgba(240,244,248,0.9); font-weight:600; font-style:italic; }
.client-info { margin-bottom:10px; }
.client-info .name { font-weight:700; color:#fff; }
.client-info .role { font-size:12px; color: rgba(240,244,248,0.75); }
.metric { color:#00d4ff; font-weight:700; font-size:13px; }

/* Legal pages */
.legal-content { color: rgba(240,244,248,0.9); }
.legal-content h3 { color:#fff; margin-top:16px; font-weight:700; }
.legal-content ul, .legal-content ol { margin-left:18px; }
.legal-content li { margin:8px 0; }

/* Contact Page */
.contact-page { max-width:1200px; }
.contact-header { text-align:center; margin-bottom:28px; animation: fadeInUp .8s both; }
.contact-header h2 { font-size:32px; margin-bottom:8px; font-weight:800; }
.contact-header .lead { color: rgba(240,244,248,0.9); }

.contact-grid { display:grid; grid-template-columns: 1fr 1.2fr; gap:32px; align-items:start; }
.contact-info { display:flex; flex-direction:column; gap:16px; }

.info-card { background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.06)); padding:22px; border-radius:14px; border-left:4px solid #00d4ff; box-shadow: 0 8px 24px rgba(0,212,255,0.1); transition: transform .3s ease, box-shadow .3s ease; animation: slideInLeft .6s both; border: 1px solid rgba(0,212,255,0.15); }
.info-card:nth-child(2) { animation-delay: .1s; }
.info-card:nth-child(3) { animation-delay: .2s; }
.info-card:hover { transform: translateY(-4px); box-shadow: 0 16px 40px rgba(0,212,255,0.2); }
.icon-box { width:48px; height:48px; border-radius:12px; background: linear-gradient(135deg,#00d4ff,#4facfe); display:flex; align-items:center; justify-content:center; font-size:24px; margin-bottom:10px; color:#0f1729; }
.info-card h3 { margin:0 0 6px; color:#fff; font-size:16px; font-weight:700; }
.info-card p { margin:0; color: rgba(240,244,248,0.85); font-size:14px; }
.info-card a { color:#00d4ff; text-decoration:none; font-weight:700; }

.contact-form-wrap { background: linear-gradient(135deg, rgba(0,212,255,0.04), rgba(79,172,254,0.03)); padding:28px; border-radius:16px; box-shadow: 0 12px 36px rgba(0,212,255,0.1); border: 1px solid rgba(0,212,255,0.15); animation: slideInRight .6s both; }
.success-message { text-align:center; padding:40px 20px; animation: fadeInUp .6s both; }
.success-icon { width:60px; height:60px; border-radius:50%; background: linear-gradient(135deg,#00d4ff,#4facfe); display:flex; align-items:center; justify-content:center; font-size:32px; margin:0 auto 16px; color:#0f1729; font-weight:800; }

.contact-form { display:flex; flex-direction:column; gap:16px; }
.form-group { display:flex; flex-direction:column; gap:8px; animation: fadeInUp .6s both; }
.form-group:nth-child(2) { animation-delay: .1s; }
.form-group:nth-child(3) { animation-delay: .2s; }

.form-group label { color:#fff; font-weight:700; font-size:14px; }
.form-group input, .form-group textarea { background: rgba(0,212,255,0.04); border:1px solid rgba(0,212,255,0.15); border-radius:10px; padding:12px 14px; color:#fff; font-family:inherit; font-size:14px; transition: all .2s ease; }
.form-group input::placeholder, .form-group textarea::placeholder { color: rgba(240,244,248,0.4); }
.form-group input:focus, .form-group textarea:focus { outline:none; background: rgba(0,212,255,0.08); border-color: rgba(0,212,255,0.4); box-shadow: 0 0 12px rgba(0,212,255,0.2); }

.submit-btn { width:100%; padding:14px 20px; margin-top:8px; font-size:16px; transition: all .3s ease; animation: fadeInUp .6s both .3s; }
.submit-btn:hover { transform: translateY(-2px); box-shadow: 0 12px 30px rgba(0,212,255,0.3); }

@keyframes slideInLeft { from { opacity:0; transform: translateX(-20px); } to { opacity:1; transform: translateX(0); } }
@keyframes slideInRight { from { opacity:0; transform: translateX(20px); } to { opacity:1; transform: translateX(0); } }

@media (max-width:820px){
  .contact-grid { grid-template-columns:1fr; gap:20px; }
}



/* Services list */
.services-page .services-list { display:grid; grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); gap:20px; margin-top:20px; }
.service-item { background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); padding:22px; border-radius:14px; display:flex; gap:16px; align-items:flex-start; box-shadow: 0 8px 24px rgba(0,212,255,0.1); transition: transform .22s ease, box-shadow .22s ease; border: 1px solid rgba(0,212,255,0.15); }
.service-item:hover { transform: translateY(-8px); box-shadow: 0 16px 40px rgba(0,212,255,0.2); }
.service-icon { width:48px; height:48px; border-radius:12px; background: linear-gradient(135deg,#00d4ff,#4facfe); display:flex; align-items:center; justify-content:center; color:#0f1729; font-weight:900; font-size:20px; flex-shrink:0; }
.service-item h3 { margin:0 0 8px; font-size:17px; color:#fff; font-weight:800; }
.service-item p { margin:0; color: rgba(240,244,248,0.85); line-height:1.6; }

.service-body { flex:1 }
.benefits { margin:12px 0 0; padding-left:18px; color: rgba(240,244,248,0.85); }
.benefits li { margin:8px 0; font-weight:500; position:relative; }
.benefits li:before { content: 'âœ”'; position:absolute; left:-18px; color:#00d4ff; font-weight:900; }

/* Credibility / Trust section */
.cred-section { margin-top:28px; padding:22px; background: linear-gradient(135deg, rgba(0,212,255,0.06), rgba(79,172,254,0.04)); border-radius:14px; border: 1px solid rgba(0,212,255,0.15); }
.cred-section h3 { margin-top:12px; color:#fff; font-weight:700; }
.cred-section p, .cred-section li { color: rgba(240,244,248,0.85); }
.cred-section ol { margin-left:18px; }
.cred-section ul { margin-left:18px; }

.team-grid { display:flex; gap:12px; margin-top:12px; flex-wrap:wrap; }
.member { display:flex; gap:10px; align-items:center; background: linear-gradient(135deg, rgba(0,212,255,0.04), rgba(79,172,254,0.03)); padding:12px; border-radius:10px; min-width:220px; border: 1px solid rgba(0,212,255,0.1); }
.avatar { width:48px; height:48px; border-radius:10px; background: linear-gradient(135deg,#00d4ff,#4facfe); display:flex; align-items:center; justify-content:center; font-weight:800; color:#0f1729; }
.meta .name { font-weight:700; color:#fff; }
.meta .role { font-size:13px; color: rgba(240,244,248,0.75); }

/* Trust & Proof */
.trust { margin-top:48px; padding:32px 24px; background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.06)); border-radius:18px; border: 1px solid rgba(0,212,255,0.15); }
.trust h2 { margin:0 0 18px; color:#fff; font-size:28px; font-weight:800; }
.trust-grid { display:flex; gap:24px; align-items:flex-start; flex-wrap:wrap; }
.trust-badges { flex:1 1 320px; display:grid; grid-template-columns:1fr; gap:12px; }
.badge { background: rgba(0,212,255,0.08); padding:14px 16px; border-radius:12px; font-weight:700; color: rgba(240,244,248,0.95); box-shadow: 0 8px 24px rgba(0,212,255,0.1); border: 1px solid rgba(0,212,255,0.2); }
.trust-right { flex:1 1 300px; display:flex; flex-direction:column; gap:14px; }
.stat-row { display:flex; gap:14px; }
.stat { background: rgba(0,212,255,0.06); padding:14px 16px; border-radius:12px; text-align:center; min-width:120px; border: 1px solid rgba(0,212,255,0.15); }
.stat .num { font-size:22px; font-weight:900; color:#00d4ff; }
.stat .label { font-size:13px; color: rgba(240,244,248,0.8); margin-top:4px; }
.testimonial { margin:0; padding:14px 16px; background: rgba(0,212,255,0.08); border-left:4px solid #00d4ff; border-radius:10px; color: rgba(240,244,248,0.95); font-weight:600; }
.client-logos { display:flex; gap:12px; margin-top:8px; flex-wrap:wrap; }
.logo { background: rgba(0,212,255,0.08); padding:10px 14px; border-radius:10px; font-weight:700; color: rgba(240,244,248,0.9); border: 1px solid rgba(0,212,255,0.2); }

@media (max-width:800px){
  .trust-grid{flex-direction:column}
  .stat-row{justify-content:space-between}
}

/* Portfolio styles */
.portfolio-header{display:flex; justify-content:space-between; align-items:center; gap:18px; margin-bottom:20px;}
.portfolio-filters{display:flex; gap:10px; flex-wrap:wrap}
.portfolio-grid{display:grid; grid-template-columns: repeat(auto-fit, minmax(240px, 1fr)); gap:18px; margin-top:10px}
.project-card{background: linear-gradient(135deg, rgba(0,212,255,0.04), rgba(79,172,254,0.03)); border-radius:12px; overflow:hidden; cursor:pointer; display:flex; flex-direction:column; transition: transform .22s ease, box-shadow .22s ease; border:1px solid rgba(0,212,255,0.12)}
.project-card:hover{transform:translateY(-6px); box-shadow: 0 16px 40px rgba(0,212,255,0.12)}
.project-thumb{height:160px; background-size:cover; background-position:center; filter:saturate(1.05);}
.project-info{padding:14px}
.project-info h3{margin:0 0 6px; font-size:18px; font-weight:800; color:#fff}
.project-info .cat{font-size:13px; color:#00d4ff; font-weight:800; margin-bottom:8px}
.project-info .summary{margin:0; color:rgba(240,244,248,0.88); font-size:14px;}

.modal-overlay{position:fixed; inset:0; background:rgba(3,6,12,0.6); display:flex; align-items:center; justify-content:center; z-index:9999; padding:20px}
.modal{background:linear-gradient(180deg, rgba(15,23,41,0.98), #071029); border-radius:12px; padding:20px; max-width:760px; width:100%; color:#fff; box-shadow:0 20px 60px rgba(0,0,0,0.6); position:relative}
.modal-thumb{height:220px; border-radius:8px; background-size:cover; background-position:center; margin-bottom:16px}
.close-btn{position:absolute; right:12px; top:12px; background:transparent; border:none; color:#fff; font-size:20px; cursor:pointer}

@media (max-width:820px){
  .portfolio-header{flex-direction:column; align-items:flex-start}
  .project-thumb{height:140px}
}


/* CTA invite */
.cta-invite { margin:40px auto 32px; padding:32px; background: linear-gradient(135deg, rgba(0,212,255,0.08), rgba(79,172,254,0.06)); border-radius:18px; text-align:center; border: 1px solid rgba(0,212,255,0.15); }
.cta-invite h2 { margin:0 0 10px; color:#fff; font-size:28px; font-weight:800; }
.cta-invite .cta-sub { margin:0 0 18px; color: rgba(240,244,248,0.9); font-weight:600; font-size:16px; }
.contact-line { margin-top:14px; color: rgba(240,244,248,0.85); font-weight:700; }
/* Mobile-specific overrides: keep desktop look on small screens */
@media (max-width:820px){
  .hero { padding:80px 16px 40px; }
  .hero h1 { font-size: clamp(24px, 8vw, 36px); line-height:1.12; }
  .hero .lead { font-size:16px; max-width:640px; }
  .ctas { flex-direction:column; gap:12px; margin-top:18px; }
  .btn { width:100%; max-width:420px; }
  .card, .tile, .testi-card, .testimonial-card, .plan { width:100%; max-width:100%; }
  .services { flex-direction:column; align-items:center; }
  .nav { padding:12px 18px; }
  .brand { font-size:18px; }
  .nav-links a { margin-left:12px; font-size:14px; }
  .blob.b1, .blob.b2, .blob.b3 { filter: blur(60px); opacity:0.45; }
  .footer-inner{flex-direction:column; align-items:center; text-align:center}
  .support-ill, .pricing-ill{width:100%; max-width:480px}
  .contact-grid { grid-template-columns:1fr; gap:20px; }
